/* dashboard css */

.tab ol, ul {
  margin-bottom: 0rem!important;
}

ul.tabs {
  overflow: hidden;
  margin: 15px 0px 0px 0px;
  padding: 0px;
}

.tab input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.tab {
  width: 100%;
  color: white;
  overflow: hidden;
}

.tab-label {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  font-size: 15px;
  padding: 1em;
  background: #56b55d;
  font-weight: bold;
  cursor: pointer;
}

.tab-label:hover {
  background: #469e4c;
}

.tab-label::after {
  content: "\276F";
  width: 1em;
  height: 1em;
  text-align: center
}

.tab-content {
  max-height: 0;
  padding: 0 1em;
  color: #2c3e50;
  background: white;
}

.tab-close {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: end;
  justify-content: flex-end;
  padding: 1em;
  font-size: 0.75em;
  background: #2c3e50;
  cursor: pointer;
}

.tab-close:hover {
  background: #1a252f;
}

.tab input:checked+.tab-label {
  background: #469e4c;
  margin: 0px;
}

.tab input:checked+.tab-label::after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.tab input:checked~.tab-content {
  max-height: 655px;
  padding: 0px 0px 0px 0px;
  margin-bottom: 5px!important;
}

ul.tab-content li {
  font-size: 15px;
  line-height: 26px;
  padding: 5px 0px 5px 15px;
  list-style: none;
  border-bottom: 1px solid #eee;
  cursor: pointer;
}

.faa-icons {
  width: 40px;
  padding-right: 5px;
}

ul.tab-content li:hover {
  background: #fafafa;
}

ul.tab-content li:last-child {
  border-bottom: 0px solid #eee;
}

.tp-prf-active {
  max-height: 655px;
  padding: 0px 0px 0px 0px;
  margin-bottom: 5px!important;
}

/* end of dashboard css */

