.loader {
  position: relative;
  width: 15px;
  height: 15px;
  margin: 0 auto;
  border-radius: 50%;
  text-indent: -9999em;
  color: #161616;
  font-size: 8px;
  opacity: .7;
  animation: loader 1s infinite ease-in-out;
  animation-delay: 0.16s;
  top: -15px;
}

.loader::before {
  left: -3.5em;
  animation-delay: 0s;
  position: absolute;
  width: 15px;
  border-radius: 50%;
  height: 15px;
  content: '';
  animation: loader 1s infinite ease-in-out;
}

.loader::after {
  left: 3.5em;
  animation-delay: 0.32s;
  position: absolute;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  content: '';
  animation: loader 1s infinite ease-in-out;
}

@keyframes loader {
  100% {
    box-shadow: 0 2.5em 0 -1.3em
  }
  80% {
    box-shadow: 0 2.5em 0 -1.3em
  }
  0% {
    box-shadow: 0 2.5em 0 -1.3em
  }
  40% {
    box-shadow: 0 2.5em 0 0
  }
}

/**
